import Vue from 'vue'
import Vuex from 'vuex'
import data from './response.json'

Vue.use(Vuex)

const Store = new Vuex.Store({
	actions: {
		// async fetchContent({ commit, getters, dispatch }) {
		// 	const res = await fetch(
		// 		'./response.json'
		// 	)
		// 	const content = await res.json()
		//
		// 	console.log(res)
		//
		// 	dispatch('')
		//
		// 	commit('', content)
		//
		// 	getters()
		// }
	},
	state: {
		content: data.content,
		status: data.status,
		sections: data.content.sections,
		panoramas: data.content.panoramas,
		videos: data.content.videos
	},
	getters: {
		dataStatus(state) {
			return state.status
		},
		allContent(state) {
			return state.content
		},
		sections(state) {
			return state.sections
		},
		panoramas(state) {
			return state.panoramas
		},
		videos(state) {
			return state.videos
		},
		allCategories(state) {
			let catArr = []
			let i = 0
			catArr[0] = {
				id: 0,
				name: '',
				active: true
			}
			for (i; i < state.sections.length; i++) {
				let category = state.sections[i]
				category.active = false
				catArr.push(category)
			}
			catArr[i + 1] = {
				id: state.sections.length + 1,
				name: '',
				active: false
			}
			return catArr
		},
		videoCategories(state) {
			// return state.content.filter((e) => e.title && e.body)
			let vCatArr = []
			for (let i = 0; i < state.sections.length; i++) {
				let newCategory = state.videos.filter((e) => e.section_id === (i + 1))
				vCatArr.push(newCategory)
			}
			return vCatArr
		}
	},
	mutations: {}
})

export default Store
