<template>
	<header class="header">
		<div v-if="preloaderActive" class="header__logo header__logo_white">
			<svg class="svg-icon" xmlns="http://www.w3.org/2000/svg">
				<use :xlink:href="`#${logoPath}`"></use>
			</svg>
		</div>
		<div v-else class="header__logo">
			<svg class="svg-icon" xmlns="http://www.w3.org/2000/svg">
				<use :xlink:href="`#${logoPath}`"></use>
			</svg>
		</div>
	</header>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
const WARNING_MAP = {}

export default {
	name: 'Header',
	props: ['preloaderActive'],

	computed: {
		logo() {
			let logoName = ''
			if (this.preloaderActive) {
				logoName = 'logo-white'
			}
			else {
				logoName = 'logo'
			}
			return logoName
		},
		logoPath() {
			try {
				const icon = require(`@/assets/svg/svg-${this.logo}.svg`)
				return icon.default ? icon.default.id : icon.id
			}
			catch (error) {
				if (!WARNING_MAP[error.message]) {
					WARNING_MAP[error.message] = true
					console.warn(error.message)
				}
			}

			return ''
		}
	}
}
</script>

<style lang="scss"></style>
