<template>
	<main class="main">
		<aside class="menu">
			<nav class="menu__categories">
				<div class="categories">
					<ul class="categories__list side-scroll">
						<li class="categories__item"
							v-for="(category, index) in categories"
							:key="index">
							<button class="btn" :class="{'btn-active': categoriesActiveList[category.id]}" @click="setActive(index)">{{category.name}}</button>
						</li>
					</ul>
				</div>
			</nav>
			<div class="menu__list scroll">
				<div class="menu__list_item" v-for="category in categories" :key="category.id">
					<div class="list-item__wrapper" v-if="(category.id > 0 && category.id < categories.length - 1) && (categoriesActiveList[category.id] || categoriesActiveList[0])">
						<h2 class="list-item__title">{{ category.name }}</h2>
						<ul class="menu__sublist">
							<li class="menu__sublist_item"
								v-for="video in videoCategories[category.id - 1]"
								:key="video.id"
								@click="initVideo(video)"
								@mouseover="startHover(video.map_id, category.id, video.id)"
								@mouseout="endHover()"
							>
								<!-- @mouserover and @mouseout  -->
								<h3 class="sublist-item__title content__title">{{ video.name }}</h3>
								<p class="sublist-item__category">{{ categories[video.section_id].name }}</p>
								<div class="sublist-item__marker content__marker" :style="{background: categoriesColors[video.section_id]}">{{video.section_id}}</div>
								<button class="sublist-item__play content__play" type="button" @click="initVideo(video)">
									<img :src="require('@/assets/images/play.svg')" alt="">
								</button>
								<div class="sublist-item__preview content__preview">
									<video :alt="video.name">
										<source :src="video.url" :type="'video/' + video.extension" />
									</video>
								</div>
							</li>
						</ul>
					</div>
					<div class="list-item__wrapper" v-if="(category.id === categories.length - 1) && (categoriesActiveList[category.id] || categoriesActiveList[0])">
						<h2 class="list-item__title">{{ category.name }}</h2>
						<ul class="menu__sublist">
							<li class="menu__sublist_item"
								v-for="panorama in panoramas"
								:key="panorama.id"
								@click="initVideo(panorama)"
								@mouseover="startHover(panorama.map_id, category.id, panorama.id)"
								@mouseout="endHover()"
							>
								<h3 class="sublist-item__title content__title">{{ panorama.name }}</h3>
								<p class="sublist-item__category">{{ categories[categories.length - 1].name }}</p>
								<div class="sublist-item__marker content__marker" :style="{background: categoriesColors[categories.length - 1]}">{{categories.length - 1}}</div>
								<button class="sublist-item__play content__play" type="button" @click="initVideo(panorama)">
									<img :src="require('@/assets/images/eye.svg')" alt="">
								</button>
								<div class="sublist-item__preview content__preview">
									<img :src="panorama.url" :alt="panorama.name" />
								</div>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div class="scrollbox">
				<button class="btn bar" type="button" @click="scrollCategory"></button>
			</div>
		</aside>
		<section class="map">
			<div class="map__wrapper">
				<div class="map__inner" :class="{'map__inner-active': +activeMap === +map.id}" v-for="(map, index) in maps" :key="'map' + index" >
					<transition name="component-fade" mode="out-in">
						<MapItem v-if="+activeMap === +map.id"
							:map-id="map.name"
							:map="map"
							:categories="categoriesActiveList"
							:active-category="activeCategory"
							:hover-point="hoverPoint"
							:categories-colors="categoriesColors"
							:map-content="mapContent[map.id]"
							:map-points-content="mapPointsContentAll[map.id]"
							:map-points="mapPointsAll[map.id]"
						/>
					</transition>
				</div>
			</div>
			<nav class="map__categories">
				<ul class="categories__list container">
					<li class="categories__item"
						:class="{'categories__item-active': categoriesActiveList[category.id]}"
						v-for="(category, index) in categories"
						:key="index">
						<button class="btn" @click="setActive(index)">
							<span class="icon icon_all" v-if="category.id === 0">
								<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 1]}"></span>
								<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 2]}"></span>
								<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 3]}"></span>
								<span class="circle" :style="{backgroundColor: categoriesColors[category.id + 4]}"></span>
							</span>
							<span class="icon icon_circle" v-if="category.id > 0" :style="{backgroundColor: categoriesColors[category.id]}">
								<img class="icon_img" v-if="category.id < categories.length - 1" :src="require('@/assets/images/section-' + category.id +'.svg')" :alt="category.id">
								<img class="icon_img" v-else-if="+category.id === categories.length - 1" src="@/assets/images/photo.svg" :alt="category.id">
							</span>
							<span class="title">{{category.name}}</span>
						</button>
					</li>
				</ul>
			</nav>
			<div class="map__arrows">
				<button class="btn btn_arrow arrow_left" type="button" @click="setPrevMap" v-show="maps.filter((e) => e.disable === false).length > 1">
					<span class="arrow__counter"
						:style="{'background': categoriesColors[activeCategory]}"
						v-if="activeCategory !== 0 && activeMap !== 0 && itemsOnPrevPages !== 0">{{itemsOnPrevPages}}</span>
					<img src="@/assets/images/arrow_right.svg" alt="Previosly">
				</button>
				<button class="btn btn_arrow arrow_right" type="button" @click="setNextMap" v-show="maps.filter((e) => e.disable === false).length > 1">
					<span class="arrow__counter"
						:style="{'background': categoriesColors[activeCategory]}"
						v-if="activeCategory !== 0 && activeMap !== (maps.length - 1) && itemsOnNextPages !== 0">{{itemsOnNextPages}}</span>
					<img src="@/assets/images/arrow_left.svg" alt="Next">
				</button>
			</div>
			<div class="map__mini">
				<div class="map__mini_wrapper">
					<img src="@/assets/images/map.jpg" alt="MiniMap">
					<div class="map__mini_target"
						:class="{'map__mini_target-active': activeMap === +map.id, 'map__mini_target-disable':  map.disable}"
						v-for="map in maps"
						:style="{top: map.mini.top + 'px', left: map.mini.left + 'px'}"
						:key="map.id"></div>
				</div>
			</div>
		</section>
		<div class="video"
			:class="{'video-active': isVideoPlayerActive || isPanoramaPlayerActive}"
			v-show="isVideoPlayerActive || isPanoramaPlayerActive"
			@mousedown="playerHintVisible = false">
			<div id="panoramaWrapper" class="video__wrapper" v-show="isPanoramaPlayerActive"></div>
			<div id="videoWrapper" class="video__wrapper" v-show="!isPanoramaPlayerActive"></div>
			<div class="video__info">
				<button class="btn btn_close" type="button" @click="destroyPlayer">
					<img :src="require('@/assets/images/closer.svg')" alt="">
				</button>
				<h2 class="video__title"></h2>
				<button class="btn btn_fullscreen" type="button">
					<img :src="require('@/assets/images/fullscreen-white.svg')" alt="">
					<img :src="require('@/assets/images/fullscreen-off-white.svg')" alt="">
				</button>
			</div>
			<div class="video__hint" v-if="playerHintVisible">
				<div class="hint__image">
					<img :src="require('@/assets/images/arrow-2-white_right.svg')" alt="Left">
					<img :src="require('@/assets/images/circle.svg')" alt="Circle">
					<img class="shape" :src="require('@/assets/images/shape.svg')" alt="Shape">
					<img :src="require('@/assets/images/arrow-2-white_left.svg')" alt="right">
				</div>
				<p class="hint__text">   <br />     </p>
			</div>
			<div class="video__next" v-show="playerNextPreloader">
				<p class="next__text"> </p>
				<h3 class="next__title">{{nextTitle}}</h3>
				<button class="btn btn_next" type="button">
					<svg id="svg" viewPort="0 0 88 88" xmlns="http://www.w3.org/2000/svg">
						<circle r="44" cx="44" cy="44" stroke-dasharray="565.48" stroke-dashoffset="0"></circle>
						<circle id="bar" r="41" cx="44" cy="44" stroke-dasharray="446.1" :stroke-dashoffset="counterBar"></circle>
					</svg>
					<img :src="require('@/assets/images/next-white.svg')" alt=" ">
				</button>
				<button class="btn btn_cancel" type="button"></button>
			</div>
			<div class="video__controls" v-show="playerControlsVisible">
				<button class="btn btn_prev" type="button">
					<img :src="require('@/assets/images/prev-white.svg')" alt=" ">
				</button>
				<button class="btn btn_play" type="button">
					<img class="replay" :src="require('@/assets/images/replay-white.svg')" alt="">
					<img class="play" :src="require('@/assets/images/play.svg')" alt="">
					<span class="pause"></span>
				</button>
				<button class="btn btn_next" type="button">
					<img :src="require('@/assets/images/next-white.svg')" alt=" ">
				</button>
				<div class="time"></div>
				<div class="timeline">
					<div class="timeline__bar"></div>
				</div>
				<div class="time time_end"></div>
				<button class="btn btn_sound" type="button">
					<img class="on" :src="require('@/assets/images/sound-on.svg')" alt=" ">
					<img class="off" :src="require('@/assets/images/sound-off.svg')" alt=" ">
				</button>
			</div>
		</div>
	</main>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */
import { mapGetters } from 'vuex'
import Clappr from '../../node_modules/clappr/dist/clappr.min'
import Video360 from '../../node_modules/clappr-video360/dist/clappr-video360.min'
import MapItem from './MapItem'

// const WARNING_MAP = {}

export default {
	name: 'Main',
	components: {
		MapItem
	},
	props: [''],
	data() {
		return {
			publicPath: process.env.BASE_URL,
			activeCategory: 0,
			activeMap: 0,
			hoverPoint: null,
			isVideoPlayerActive: false,
			isPanoramaPlayerActive: false,
			player: null,
			maps: [
				{
					id: 0,
					name: 'main',
					disable: false,
					mini: {
						top: 31,
						left: 55
					}
				},
				{
					id: 1,
					name: 'second',
					disable: false,
					mini: {
						top: 46,
						left: 46
					}
				},
				{
					id: 2,
					name: 'third',
					disable: false,
					mini: {
						top: 74,
						left: 32
					}
				}
			],
			playerHintVisible: false,
			playerControlsVisible: false,
			playerCurrentVideoID: null,
			currentIndex: 0,
			nextTitle: '',
			playerNextPreloader: false
		}
	},
	computed: {
		...mapGetters(['allContent', 'dataStatus', 'sections', 'panoramas', 'videos', 'videoCategories']),
		categories() {
			let catArr = []
			let i = 0
			catArr[0] = {
				id: 0,
				name: ''
			}
			for (i = 0; i < this.sections.length; i++) {
				let category = this.sections[i]
				catArr.push(category)
			}
			catArr[i + 1] = {
				id: this.sections.length + 1,
				name: ''
			}
			return catArr
		},
		categoriesActiveList() {
			let catArrActive = []
			let catArr = this.categories
			let activeIndex = this.activeCategory
			for (let i = 0; i < catArr.length; i++) {
				if (activeIndex === +catArr[i].id) {
					catArrActive[i] = true
				}
				else {
					catArrActive[i] = false
				}
			}
			return catArrActive
		},
		categoriesColors() {
			let catColors = []
			let categories = this.categories
			for (let i = 0; i < categories.length; i++) {
				switch (categories[i].id) {
				case '1':
					catColors.push('#eec05e')
					break
				case '2':
					catColors.push('#63c678')
					break
				case '3':
					catColors.push('#bf93e8')
					break
				case '4':
					catColors.push('#73b5d0')
					break
				case (categories.length - 1):
					catColors.push('#eb863e')
					break
				default:
					catColors.push('transparent')
					break
				}
			}
			return catColors
		},
		mapsActiveList() {
			let mapArr = this.maps
			let mapsActiveArr = []
			for (let i = 0; i < mapArr.length; i++) {
				mapsActiveArr.push(mapArr[i].active)
			}
			return mapsActiveArr
		},
		mapPointsContentAll() {
			let pointsArray = []
			for (let m = 0; m < this.mapContent.length; m++) {
				let map = this.mapContent[m].slice()
				let newMap = []
				for (let i = 0; i < map.length; i++) {
					let oldArr = map[i].slice()
					let newArr = []
					while (oldArr.length > 0) {
						let f = oldArr[0]
						let n = oldArr.filter((e) => (e.left === f.left && e.top === f.top))
						newArr.push(n)
						for (let j = 0; j < n.length; j++) {
							let index = oldArr.indexOf(n[j])
							if (index > -1) {
								oldArr.splice(index, 1)
							}
						}
					}
					newMap.push(newArr)
				}
				pointsArray.push(newMap)
			}
			return pointsArray
		},
		mapPointsAll() {
			let pointsAll = this.mapPointsContentAll
			let pointsArray = []
			for (let m = 0; m < pointsAll.length; m++) { // m 0,1,2
				let source = pointsAll[m]
				let id = 0
				let newArr = []
				for (let i = 0; i < source.length; i++) { // i 0,1,2,3,4
					let s = source[i]
					for (let j = 0; j < source[i].length; j++) { // j 0+
						let sj = s[j]
						let obj = {}
						obj.id = id
						id += 1
						obj.left = sj[0].left
						obj.top = sj[0].top
						if (sj[0].section_id) {
							obj.categoryID = sj[0].section_id
						}
						else {
							obj.categoryID = 5
						}
						obj.isHovering = false
						obj.isActive = false
						obj.isVisited = false
						obj.content = sj
						newArr.push(obj)
					}
				}
				pointsArray.push(newArr)
			}
			return pointsArray
		},
		mapContent() {
			let mapArr = []
			for (let i = 0; i < 3; i++) {
				let newMapVideos = this.videos.filter((e) => e.map_id === i)
				let newMapPanoramas = this.panoramas.filter((e) => e.map_id === i)
				let catArr = []
				for (let j = 0; j < this.categories.length - 1; j++) {
					if (j === this.categories.length - 2) {
						catArr.push(newMapPanoramas)
					}
					else {
						let newCategory = newMapVideos.filter((e) => e.section_id === (j + 1))
						catArr.push(newCategory)
					}
				}
				mapArr.push(catArr)
			}
			return mapArr
		},
		itemsOnPrevPages() {
			let items = 0
			if (this.activeCategory > 0 && this.activeMap > 0) {
				for (let i = 0; i < this.activeMap; i++) {
					let m = this.mapContent[i]
					let n = m[this.activeCategory - 1]
					items += n.length
				}
			}
			return items
		},
		itemsOnNextPages() {
			let items = 0
			if (this.activeCategory > 0 && this.activeMap < this.maps.length - 1) {
				for (let i = this.activeMap + 1; i < this.maps.length; i++) {
					let m = this.mapContent[i]
					let n = m[this.activeCategory - 1]
					items += n.length
				}
			}
			return items
		},
		counterBar() {
			let bar = 0
			let c = Math.PI * (71 * 2)

			if (bar < 0) {
				bar = 0
			}
			if (bar > 100) {
				bar = 100
			}

			bar = ((100 - this.currentIndex) / 100) * c
			return bar
		}
	},
	mounted() {
		// Scroll
		if (document.querySelector('.scroll')) {
			let slider = document.querySelector('.scroll')
			let scrollBox = slider.nextSibling
			let scrollBar = scrollBox.firstChild
			let parent = slider.parentNode
			setTimeout(() => {
				scrollBar.style.height = `${slider.offsetHeight / parent.offsetHeight}%`
			}, 6000)
			// eslint-disable-next-line no-inner-declarations
			function ScrollBox(e) {
				let target = e.target
				while (!target.classList.contains('scroll')) {
					target = target.parentNode
				}
				let container = target.parentNode
				let limit = container.offsetHeight - target.offsetHeight
				scrollBar.style.height = `${target.offsetHeight / container.offsetHeight}%`
				let startY = () => {
					let y = target.style.transform
					let start = y.indexOf('(')
					let end = y.indexOf('p')
					if (y !== '') {
						y = y.substring(start + 1, end)
					}
					else {
						y = 0
					}
					return +y
				}
				let scrollTop = startY() + (e.deltaY * -1)
				if (scrollTop > 0) {
					scrollTop = 0
				}
				if (limit <= 0) {
					if (scrollTop <= limit) {
						scrollTop = limit
					}
				}
				else {
					scrollTop = 0
				}
				let translateY = `translateY(${scrollTop}px)`
				let scrollBoxTranslateY = `translateY(${(scrollTop / limit) * (window.innerHeight - 78 - scrollBar.offsetHeight)}px)`
				target.style.transform = translateY
				scrollBar.style.transform = scrollBoxTranslateY
			}
			slider.onwheel = ScrollBox
		}
		if (document.querySelector('.side-scroll')) {
			const slider = document.querySelector('.side-scroll')
			let isDown = false
			let startX = 0
			let scrollLeft = 0
			let translateX
			let sliderWidth = slider.offsetWidth
			let sliderContainer = slider.parentNode
			let containerWidth = sliderContainer.offsetWidth
			let limit = sliderWidth - containerWidth
			slider.addEventListener('mousedown', (e) => {
				isDown = true
				slider.classList.add('active')
				startX = e.pageX - scrollLeft
			})
			slider.addEventListener('mouseleave', () => {
				isDown = false
				slider.classList.remove('active')
			})
			slider.addEventListener('mouseup', () => {
				isDown = false
				slider.classList.remove('active')
			})
			slider.addEventListener('mousemove', (e) => {
				if (!isDown) return
				e.preventDefault()
				const x = e.pageX - scrollLeft
				const walk = x - startX // scroll-fast
				scrollLeft += walk * -1
				if (scrollLeft < 20) {
					scrollLeft = 0
				}
				else if (scrollLeft >= 20 && scrollLeft >= limit) {
					scrollLeft = limit
				}
				translateX = `translateX(${scrollLeft * -1}px)`
				slider.style.transform = translateX
			})
		}
		//
	},
	methods: {
		// ...mapActions(['']),
		setActive(index) {
			const slider = document.querySelector('.scroll')
			slider.style.transform = 'translateY(0px)'
			this.activeCategory = index
			const data = this.mapContent
			for (let i = 0; i < this.maps.length; i++) {
				let dataMap = data[i]
				this.maps[i].disable = false
				if (index > 0) {
					this.maps[i].disable = (dataMap[index - 1].length === 0)
				}
				else {
					this.maps[i].disable = false
				}
			}
			if (this.maps[this.activeMap].disable) {
				this.activeMap = 0
			}
			//
			let sideSlider = document.querySelector('.side-scroll')
			let sliderButtons = sideSlider.querySelectorAll('li')
			let sliderContainer = sideSlider.parentNode
			let limit = sideSlider.offsetWidth - sliderContainer.offsetWidth
			let button = sliderButtons[this.activeCategory]
			let coords = button.getBoundingClientRect()
			let x = coords.x
			let width = coords.width
			let sliderCoords = sliderContainer.getBoundingClientRect()
			let sliderX = sliderCoords.x
			let sliderW = sliderCoords.width
			let sliderEnd = sliderX + sliderW
			let buttonEnd = x + width
			let translateX = 0
			if (buttonEnd > sliderEnd) {
				let oldTranslate = sideSlider.style.transform
				if (oldTranslate !== '') {
					let start = oldTranslate.indexOf('(')
					let end = oldTranslate.indexOf('p')
					oldTranslate = oldTranslate.substring(start + 1, end)
				}
				else { oldTranslate = 0 }
				let newTranslate = oldTranslate - 50
				if (newTranslate <= (limit * -1)) {
					newTranslate = limit * -1
					translateX = `translateX(${newTranslate}px)`
				}
				else {
					let difference = buttonEnd - sliderEnd
					translateX = `translateX(${newTranslate - difference}px)`
				}
				sideSlider.style.transform = translateX
			}
			else if (x < sliderX) {
				let oldTranslate = sideSlider.style.transform
				if (oldTranslate !== '') {
					let start = oldTranslate.indexOf('(')
					let end = oldTranslate.indexOf('p')
					oldTranslate = oldTranslate.substring(start + 1, end)
				}
				else { oldTranslate = 0 }
				let newTranslate = +oldTranslate + 50
				if (newTranslate < 0) {
					let difference = sliderX - x
					translateX = `translateX(${newTranslate + difference}px)`
				}
				else { translateX = `translateX(0px)` }
				sideSlider.style.transform = translateX
			}
			if (this.activeCategory === 0) {
				translateX = `translateX(0px)`
				sideSlider.style.transform = translateX
			}
			if (this.activeCategory === this.categories().length - 1) {
				translateX = `translateX(${limit}px)`
				sideSlider.style.transform = translateX
			}
		},
		setPrevMap() {
			if (this.activeMap > 0) {
				if (!this.maps[this.activeMap - 1].disable) {
					this.activeMap -= 1
				}
				else if (!this.maps[this.activeMap - 2].disable) {
					this.activeMap -= 2
				}
			}
			else if (!this.maps[this.maps.length - 1].disable) {
				this.activeMap = this.maps.length - 1
			}
			else if (!this.maps[this.maps.length - 2].disable) {
				this.activeMap = this.maps.length - 2
			}
			else {
				this.activeMap = 0
			}
		},
		setNextMap() {
			if (this.activeMap < this.maps.length - 1) {
				if (!this.maps[this.activeMap + 1].disable) {
					this.activeMap += 1
				}
				else if (this.activeMap === 0 && !this.maps[this.activeMap + 2].disable) {
					this.activeMap += 2
				}
				else {
					this.activeMap = 0
				}
			}
			else {
				this.activeMap = 0
			}
		},
		initVideo(data) {
			let container = document.querySelector('.video')
			let closeButton = container.querySelector('.btn_close')
			let fullscreenButton = container.querySelector('.btn_fullscreen')
			let title = container.querySelector('.video__title')
			title.innerText = data.name
			this.playerHintVisible = true
			// init
			if (data.extension === 'jpg') {
				this.isPanoramaPlayerActive = true
				// eslint-disable-next-line no-undef
				this.player = pannellum.viewer('panoramaWrapper', {
					type: 'equirectangular',
					// panorama: data.url,
					panorama: `${this.publicPath}img/panorama.jpg`,
					autoPlay: true,
					autoLoad: true,
					showControls: false
				})

				window.onresize = () => {
					this.player.setSize({
						height: window.innerHeight,
						width: window.innerWidth
					})
				}

				closeButton.onclick = () => {
					this.player.destroy()
				}

				fullscreenButton.onclick = () => {
					this.player.toggleFullscreen()
				}
			}
			else {
				this.isVideoPlayerActive = true
				let categoryAllVideo = this.videoCategories[data.section_id - 1]
				let currentIndex = categoryAllVideo.filter((e) => e.id === data.id).indexOf()
				if (currentIndex < 0) { currentIndex = 0 }
				this.playerCurrentVideoID = currentIndex
				console.log(categoryAllVideo[this.playerCurrentVideoID + 1])
				this.playerControlsVisible = true
				let controls = document.querySelector('.video__controls')
				let currentTime = controls.querySelector('.time')
				let leftTime = controls.querySelector('.time_end')
				let timeline = controls.querySelector('.timeline')
				let bar = timeline.querySelector('.timeline__bar')
				let playButton = controls.querySelector('.btn_play')
				//
				let player = new Clappr.Player({
					// source: data.url,
					source: `${this.publicPath}img/MISis_SCN_draft.mp4`,
					plugins: {
						container: [Video360],
						loadExternalPluginsFirst: true
					},
					width: window.innerWidth,
					height: window.innerHeight,
					events: {
						onTimeUpdate: () => {
							let currentTimeAll = Math.floor(player.getCurrentTime())
							let currentTimeSeconds = currentTimeAll % 60
							let currentTimeMinutes = (currentTimeAll - currentTimeSeconds) / 60
							let timeAll = Math.floor(player.getDuration())
							let leftTimeAll = (timeAll - currentTimeAll)
							let leftTimeSeconds = leftTimeAll % 60
							let leftTimeMinutes = (leftTimeAll - leftTimeSeconds) / 60
							if (currentTimeSeconds < 10) { currentTimeSeconds = `0${currentTimeSeconds}` }
							if (leftTimeSeconds < 10) { leftTimeSeconds = `0${leftTimeSeconds}` }
							currentTime.innerText = `${currentTimeMinutes}:${currentTimeSeconds}`
							leftTime.innerText = `-${leftTimeMinutes}:${leftTimeSeconds}`
							bar.style.width = `${(currentTimeAll / timeAll) * 100}%`
						},
						onEnded: () => {
							let currentTimeAll = player.getDuration()
							let currentTimeSeconds = currentTimeAll % 60
							let currentTimeMinutes = (currentTimeAll - currentTimeSeconds) / 60
							let buttonNext = document.querySelector('.video__next .btn_next')
							let buttonPrev = document.querySelector('.video__next .btn_prev')
							currentTime.innerText = `${currentTimeMinutes}:${currentTimeSeconds}`
							leftTime.innerText = '00:00'
							bar.style.width = '100%'
							playButton.classList.add('btn_play-replay')
							if (this.playerCurrentVideoID < categoryAllVideo.length - 1) {
								this.playerNextPreloader = true
								this.nextTitle = categoryAllVideo[this.playerCurrentVideoID + 1].name
								let interval = 5000 / 100
								let timing = setInterval(() => {
									if (this.currentIndex < 100) {
										this.currentIndex = +this.currentIndex + 1
									}
									else {
										clearInterval(timing)
										if (this.playerCurrentVideoID === 0) {
											buttonPrev.classList.remove('btn_prev-disable')
										}
										this.playerCurrentVideoID += 1
										if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
											buttonNext.classList.add('btn_prev-disable')
										}
										let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
										// player.load(nextVideo.url)
										title.innerText = nextVideo.name
										player.load(`${this.publicPath}img/MISis_SCNnext_draft.mp4`)
										player.play()
										playButton.classList.remove('btn_play-replay')
										this.playerNextPreloader = false
										this.currentIndex = 0
									}
								}, interval)
								let buttonCancel = document.querySelector('.video__next .btn_cancel')
								buttonCancel.onclick = () => {
									clearInterval(timing)
									this.playerNextPreloader = false
									this.currentIndex = 0
								}
								playButton.onclick = () => {
									clearInterval(timing)
									this.playerNextPreloader = false
									this.currentIndex = 0
									player.play()
									playButton.classList.remove('btn_play-replay')
								}
								buttonNext.onclick = () => {
									clearInterval(timing)
									if (this.playerCurrentVideoID === 0) {
										buttonPrev.classList.remove('btn_prev-disable')
									}
									this.playerCurrentVideoID += 1
									if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
										buttonNext.classList.add('btn_prev-disable')
									}
									let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
									player.load(`${this.publicPath}img/MISis_SCNnext_draft.mp4`)
									// player.load(nextVideo.url)
									title.innerText = nextVideo.name
									this.playerNextPreloader = false
									this.currentIndex = 0
									player.play()
									playButton.classList.remove('btn_play-replay')
								}
							}
						}
					},
					autoplay: true
				})
				player.attachTo(container.querySelector('#videoWrapper'))
				player.play()
				player.unmute()
				player.getPlugin('click_to_pause').disable()
				//
				// Show and hide controls
				let timer = setTimeout(() => {
					controls.classList.remove('video__controls-hide')
				}, 2000)
				window.onmousemove = (e) => {
					clearTimeout(timer)
					let target = e.target
					let isControls = false
					while (target.classList.contains('video')) {
						if (target.classList.contains('video__controls')) {
							isControls = true
						}
						target = target.parentNode
					}
					if (controls.classList.contains('video__controls-hide') || isControls || e.target.classList.contains('video__controls') || e.target.closest('video__controls')) {
						clearTimeout(timer)
						controls.classList.remove('video__controls-hide')
					}
					else {
						timer = setTimeout(() => {
							controls.classList.add('video__controls-hide')
						}, 2000)
					}
				}
				//
				// Next and Previous videos
				let nextVideoButton = controls.querySelector('.btn_next')
				let prevVideoButton = controls.querySelector('.btn_prev')
				if (this.playerCurrentVideoID === 0) {
					prevVideoButton.classList.add('btn_prev-disable')
				}
				prevVideoButton.onclick = () => {
					if (this.playerCurrentVideoID > 0) {
						if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
							nextVideoButton.classList.remove('btn_next-disable')
						}
						this.playerCurrentVideoID -= 1
						let prevVideo = categoryAllVideo[this.playerCurrentVideoID]
						player.load(`${this.publicPath}img/MISis_SCN_draft.mp4`)
						// player.load(prevVideo.url)
						title.innerText = prevVideo.name
						player.play()
						playButton.classList.remove('btn_play-replay')
					}
				}
				//
				if (this.playerCurrentVideoID === categoryAllVideo.length - 1) {
					nextVideoButton.classList.add('btn_next-disable')
				}
				nextVideoButton.onclick = () => {
					if (this.playerCurrentVideoID < categoryAllVideo.length - 1) {
						if (this.playerCurrentVideoID === 0) {
							prevVideoButton.classList.remove('btn_prev-disable')
						}
						this.playerCurrentVideoID += 1
						let nextVideo = categoryAllVideo[this.playerCurrentVideoID]
						player.load(`${this.publicPath}img/MISis_SCNnext_draft.mp4`)
						// player.load(nextVideo.url)
						title.innerText = nextVideo.name
						player.play()
					}
				}
				//
				// Play and pause video
				playButton.onclick = () => {
					if (playButton.classList.contains('btn_play-paused')) {
						playButton.classList.toggle('btn_play-paused')
						player.play()
					}
					else if (playButton.classList.contains('btn_play-replay')) {
						playButton.classList.toggle('btn_play-replay')
						player.play()
					}
					else {
						playButton.classList.toggle('btn_play-paused')
						player.pause()
					}
				}
				//
				// Mute and unmute sound
				let soundButton = controls.querySelector('.btn_sound')
				soundButton.onclick = () => {
					if (soundButton.classList.contains('btn_sound-muted')) {
						soundButton.classList.toggle('btn_sound-muted')
						player.unmute()
					}
					else {
						soundButton.classList.toggle('btn_sound-muted')
						player.mute()
					}
				}
				//
				// Set new time
				timeline.onclick = (e) => {
					let x = e.pageX
					let timeX = timeline.getBoundingClientRect().x
					let timeWidth = timeline.offsetWidth
					let percent = ((x - timeX) / timeWidth) * 100
					player.seekPercentage(percent)
					bar.style.width = `${percent}%`
				}
				//
				// Close player
				closeButton.onclick = () => {
					this.playerControlsVisible = false
					this.playerCurrentVideoID = null
					title.innerText = ''
					player.destroy()
					playButton.classList.remove('btn_play-paused', 'btn_play-replay')
					// source: `${this.publicPath}img/${data.filename}.${data.extension}`
				}
			}
		},
		destroyPlayer() {
			this.isVideoPlayerActive = false
			this.isPanoramaPlayerActive = false
			this.player.destroy()
		},
		startHover(mapID, categoryID, itemID) {
			if (this.activeMap === mapID) {
				let m = this.mapPointsAll[+mapID]
				let c = m.filter((e) => (+e.categoryID === +categoryID))
				let point
				for (let i = 0; i < c.length; i++) {
					let item = c[i]
					if (item.content.filter((e) => (+e.id === +itemID)).length > 0) {
						point = item.id
					}
				}
				this.hoverPoint = +point
			}
		},
		endHover() {
			this.hoverPoint = null
		},
		scrollCategory() {
			let slider = document.querySelector('.scroll')
			let scrollBox = slider.nextSibling
			let scrollBar = scrollBox.firstChild
			let isActive = false
			scrollBar.onmousedown = () => {
				isActive = true
				scrollBox.classList.add('scrollbox-active')
			}
			scrollBar.onmouseup = () => {
				isActive = false
				scrollBox.classList.remove('scrollbox-active')
			}
			scrollBox.onmouseleave = () => {
				isActive = false
				scrollBox.classList.remove('scrollbox-active')
			}
			scrollBox.onmousemove = (e) => {
				if (isActive) {
					let target = slider
					let container = target.parentNode
					let limit = container.offsetHeight - target.offsetHeight
					let moveY = e.clientY - 78
					let scrollTop = Math.floor((moveY / (scrollBox.offsetHeight - scrollBar.offsetHeight)) * limit)
					if (scrollTop > 0) {
						scrollTop = 0
					}
					if (limit <= 0) {
						if (scrollTop <= limit) {
							scrollTop = limit
						}
					}
					else {
						scrollTop = 0
					}
					let translateY = `translateY(${scrollTop}px)`
					let scrollBoxTranslateY = `translateY(${moveY}px)`
					if (moveY > scrollBox.offsetHeight - scrollBar.offsetHeight) {
						scrollBoxTranslateY = `translateY(${scrollBox.offsetHeight - scrollBar.offsetHeight}px)`
					}
					if (moveY < 0) {
						scrollBoxTranslateY = `translateY(0px)`
					}
					slider.style.transform = translateY
					scrollBar.style.transform = scrollBoxTranslateY
				}
			}
		}
	}

}
</script>

