<template>
	<div id="app">
<!--		<aside>-->
<!--			{{ $t('tmpStuff') }}-->
<!--		</aside>-->
		<Header :preloader-active="preloaderActive" />
		<transition-group name="component-fade" mode="out-in">
			<Preloader v-if="preloaderActive" :counter-duration="1000" :preloader-active.sync="preloaderActive" key="preloader" />
			<Main v-show="!preloaderActive" key="main" />
		</transition-group>

	</div>
</template>

<script>
import Header from './components/Header'
import Preloader from './components/Preloader'
import Main from './components/Main'

export default {
	name: 'App',
	components: {
		Header,
		Preloader,
		Main
	},
	data() {
		return {
			publicPath: process.env.BASE_URL,
			preloaderActive: true
		}
	},
	mounted() {
		// Panellum documentation https://pannellum.org/documentation/overview/
		let pannellum = document.createElement('script')
		pannellum.setAttribute('type', 'text/javascript')
		pannellum.setAttribute('src', 'https://cdn.jsdelivr.net/npm/pannellum@2.5.6/build/pannellum.js')
		pannellum.async = true
		document.head.appendChild(pannellum)
		//
		let pannellumStyles = document.createElement('link')
		pannellumStyles.setAttribute('rel', 'stylesheet')
		pannellumStyles.setAttribute('href', 'https://cdn.jsdelivr.net/npm/pannellum@2.5.6/build/pannellum.css')
		pannellumStyles.async = true
		document.head.appendChild(pannellumStyles)
		//
		// let Clappr = document.createElement('script')
		// Clappr.setAttribute('type', 'text/javascript')
		// Clappr.setAttribute('src', 'https://cdn.jsdelivr.net/npm/@clappr/player@latest/dist/clappr.min.js')
		// Clappr.async = true
		// document.head.appendChild(Clappr)
		//
	}
}
</script>

<style lang="scss">
	.component-fade-enter-active, .component-fade-leave-active {
		transition: opacity .3s ease;
	}
	.component-fade-enter, .component-fade-leave-to
		/* .component-fade-leave-active   2.1.8 */ {
		opacity: 0;
	}
</style>
